 .task.done {
     text-decoration: line-through !important;
 }
.add-task {
    position: fixed;
    bottom: 3px;
    left: 0;
    right: 0;
    width: 100%;
    z-index: 2;
    font-size: 2rem !important;
}
.clear-btn {
    position: fixed;
    top: 0;
    right: 0;
    border-radius: 50% !important;
}
